<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    created() {
      let clientWidth = document.documentElement.clientWidth || document.body.clientWidth
      if (clientWidth <= 780) {
        location.href = 'https://loong.ph/app/#/'
      }
      let vCodeId = sessionStorage.getItem("vCodeId");
      if (!vCodeId) {
        this.GenNonDuplicateID()
      }
    },
    methods: {
      GenNonDuplicateID() { // 生成不会重复的ID
        let idStr = Date.now().toString(36)
        idStr = 'h5-' + idStr + Math.random().toString(36).substr(3)
        sessionStorage.setItem("vCodeId", idStr);
        console.log(idStr)
      }
    }
  }
</script>


