import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'qs'
import router from '../router'
import { getCookie } from '../utils'
import createPersistedState from 'vuex-persistedstate'
Vue.prototype.$http = axios

// 曹旭
// axios.defaults.baseURL = 'http://192.169.0.209:8080/waimai'
// axios.defaults.baseURL = 'http://192.168.1.115:8080/waimai'
// axios.defaults.baseURL = 'http://192.168.0.137:8080/waimai'
// axios.defaults.baseURL = 'http://192.168.0.196:8080/waimai'
// 王萌
axios.defaults.baseURL = 'https://sit.loong.ph/waimai'
// axios.defaults.baseURL = '/api'
// axios.defaults.baseURL = 'http://192.168.0.122:8080'
// axios.defaults.baseURL = 'https://loong.ph/waimai'
axios.defaults.timeout = 10000
axios.defaults.withCredentials = true

axios.interceptors.request.use((config) => {
  // const token = getCookie('session'); //获取Cookie
  // config.data = JSON.stringify(config.data);
  // config.headers = {
  //   'Content-Type':'application/x-www-form-urlencoded' //设置跨域头部
  // };
  // if (token) {
  //   config.params = {'token': token} //后台接收的参数，后面我们将说明后台如何接收
  // }
  let lang = getCookie('lang')
  config.headers['Accept-Language'] = lang
  if (config.method === 'post') {
    config.data = qs.stringify(config.data)
  }
  return config
})
// http response 拦截器
axios.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          // 返回 401 清除token信息并跳转到登录页面
          // store.commit(types.LOGOUT);
          Vue.prototype.$message({
            showClose: true,
            message: '您还未登录',
            type: 'warning'
          });
          router.push('/account/login')
          break
        case 410:
          Vue.prototype.$message({
            showClose: true,
            message: '您的账号在其他设备登录',
            type: 'warning'
          });
          router.push('/account/login');
      }
    }
    return Promise.reject(error.response.data)   // 返回接口返回的错误信息
  });

Vue.use(Vuex)

import user from './user'
import seller from './seller'
import order from './order'

export default new Vuex.Store({
  modules: {
    user, seller, order
  },
  plugins: [createPersistedState()]
})




